import * as React from 'react';

import { HomeContainer } from './home.styled';
import { useAppContext } from '../../context/app.context';

type IHomeProps = {};
const Home = (_: IHomeProps) => {
  const [state] = useAppContext();
  return (
    <HomeContainer>
      {state.tickets.map((item: any) => {
        return (
          <div className="row">
            {item.map((_item: any) => {
              return <div className="col">{_item.type}</div>;
            })}
          </div>
        );
      })}
    </HomeContainer>
  );
};
export default Home;
